<template>
  <div>
    <div>
      <h1 class="main-title"></h1>
    </div>
    <div class="nav-wrapper">
      <nav class="nav-container">
        <router-link
          to="/WH"
          class="nav"
          :class="{ active: $route.path.startsWith('/WH') }"
          ></router-link
        >
        <router-link
          to="/FirstTierCity"
          class="nav"
          :class="{ active: $route.path.startsWith('/FirstTierCity') }"
          ></router-link
        >
        <router-link
          to="/MajorProvincial"
          class="nav"
          :class="{ active: $route.path.startsWith('/MajorProvincial') }"
          ></router-link
        >
        <router-link
          to="/NationWide"
          class="nav"
          :class="{ active: $route.path.startsWith('/NationWide') }"
          ></router-link
        >
      </nav>
    </div>

    <!--  loading  -->
    <div v-if="loading" class="loading-overlay">
      <div class="spinner"></div>
      <div class="loading-text">...</div>
    </div>

    <router-view v-if="!loading"></router-view>
  </div>
</template>

<script>
import { loadChartData } from "@/config/dataLoader.js";
import { WHGDPCharts } from "@/config/chartMetaWH.js";
import { CityGDPCharts } from "@/config/chartMetaCity.js";
import { ProvincialGDPCharts } from "@/config/chartMetaProvince.js";
import { GDPCharts } from "@/config/chartMetaNation.js";
import { logger } from "./utils/Logger";

export default {
  name: "App",
  data() {
    return {
      loading: true,
    };
  },
  async mounted() {
    try {
      // 
      // logger.debug("...");
      
      // 
      const configs = [
        { source: WHGDPCharts.source, name: '' },
        { source: CityGDPCharts.source, name: '' },
        { source: ProvincialGDPCharts.source, name: '' },
        { source: GDPCharts.source, name: '' }
      ];

      // 
      const currentPath = window.location.pathname || '/WH';
      const currentConfig = configs.find(config => 
        currentPath.includes('WH') ? config.name === '' :
        currentPath.includes('FirstTierCity') ? config.name === '' :
        currentPath.includes('MajorProvincial') ? config.name === '' :
        currentPath.includes('NationWide') ? config.name === '' :
        config.name === '' // 
      );

      if (currentConfig) {
        // logger.debug(`: ${currentConfig.name}`);
        await loadChartData(currentConfig.source);
      }

      // 
      setTimeout(async () => {
        const otherConfigs = configs.filter(config => config !== currentConfig);
        for (const config of otherConfigs) {
          try {
            // logger.debug(`: ${config.name}`);
            await loadChartData(config.source);
          } catch (e) {
            logger.warn(`: ${config.name}`, e);
          }
        }
        // logger.debug("");
      }, 2000); // 2

      //  loading
      this.loading = false;
      logger.debug("");
    } catch (e) {
      logger.error("", e);
      //  loading
      this.loading = false;
    }
  },
};
</script>

